import React, { Component } from "react";
import {
    Switch,
    Route,
    withRouter,
    RouteComponentProps,
    Redirect,
} from "react-router-dom";

import Background from './components/Background'
import Header from './components/Header'
import ListSignIn from './pages/ListSignIn'
import ListSignOut from './pages/ListSignOut'


type TProps = RouteComponentProps

class App extends Component<TProps> {

    render() {

        return (
            <>
                <Background />
                <Header />
                    <Switch>
                        <Route exact path='/demo/famly'>
                            <Redirect to='/demo/famly/signin'/>
                        </Route>
                        <Route exact path='/demo/famly/signin'>
                            <ListSignIn/>
                        </Route>
                        <Route exact path='/demo/famly/signout'>
                            <ListSignOut />
                        </Route>
                    </Switch>
            </>
        );
    }
}


export default withRouter(App);